import React from "react";
import { useState, useRef, useEffect } from "react";
import { useDispatch } from "react-redux";
import { registerUser } from "../_actions/user_action";
import { useNavigate } from "react-router-dom";
import RegisterStyle from "../css/Register.module.css";

function Register() {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [passwordCheck, setPasswordCheck] = useState("");
    const [isValidEmail, setIsValidEmail] = useState(true);
    const [isValidPassword, setIsValidPassword] = useState(true);
    const [isValidPasswordCheck, setIsValidPasswordCheck] = useState(true);
    const [isValidName, setIsValidName] = useState(true);
    const [isValidPhone, setIsValidPhone] = useState(true);
    const [agreeCheckAll, setAgreeCheckAll] = useState(false);
    const [termsChecked, setTermsChecked] = useState(false);
    const [privacyChecked, setPrivacyChecked] = useState(false);
    const [marketingChecked, setMarketingChecked] = useState(false);

    const emailInputRef = useRef(null);
    const passwordInputRef = useRef(null);
    const passwordCheckInputRef = useRef(null);
    const nameInputRef = useRef(null);
    const phoneInputRef = useRef(null);

    const navigate = useNavigate();

    const dispatch = useDispatch();

    //    
    const handleEmailInputChange = (e) => {
        const emailVal = e.target.value;
        setEmail(emailVal);
        const email_rule = /^[a-z A-Z 0-9._-]+@[a-zA-Z0-9.-]+\.[a-zA-Z]{2,4}$/i;
        const isValid = email_rule.test(emailVal);
        setIsValidEmail(isValid);
    };

    //  
    const handlePasswordInputChange = (e) => {
        const passwordVal = e.target.value;

        setPassword(passwordVal);
        const password_rule = /^(?=.*[A-Za-z])(?=.*\d|.*[\p{P}\p{S}])[A-Za-z\d\p{P}\p{S}]{8,15}$/u;
        if (!password_rule.test(passwordVal)) {
            setIsValidPassword(false);
        } else {
            setIsValidPassword(true);
        }
        if (passwordCheck !== passwordVal) {
            setIsValidPasswordCheck(false);
        }
    };

    //   
    const handlePasswordCheckInputChange = (e) => {
        const passwordCheckVal = e.target.value;
        setPasswordCheck(passwordCheckVal);
        if (passwordCheckVal === password) {
            setIsValidPasswordCheck(true);
        } else {
            setIsValidPasswordCheck(false);
        }
    };

    //  
    const handleNameInputChange = (e) => {
        const nameVal = e.target.value;
        const nameRule = /^[-]+$/;
        if (nameVal.length < 2 || !nameRule.test(nameVal)) {
            setIsValidName(false);
        } else {
            setIsValidName(true);
        }
    };

    //   +  
    const handlePhoneInputChange = (e) => {
        //  
        let value = e.target.value;
        value = value.replace(/[^0-9]/g, "");

        if (value.length <= 3) {
            e.target.value = value;
        } else if (value.length <= 7) {
            e.target.value = `${value.slice(0, 3)}-${value.slice(3)}`;
        } else {
            e.target.value = `${value.slice(0, 3)}-${value.slice(3, 7)}-${value.slice(7, 11)}`;
        }

        const phoneVal = e.target.value;
        if (phoneVal.length < 13) {
            setIsValidPhone(false);
        } else {
            setIsValidPhone(true);
        }
    };

    //    
    useEffect(() => {
        const allChecked = termsChecked && privacyChecked && marketingChecked;
        setAgreeCheckAll(allChecked);
    }, [termsChecked, privacyChecked, marketingChecked]);

    const handleAgreeCheckAllChange = (e) => {
        const checked = e.target.checked;

        setAgreeCheckAll(checked);
        setTermsChecked(checked);
        setPrivacyChecked(checked);
        setMarketingChecked(checked);
        console.log(checked);
    };

    //    
    const handleTermsChange = (e) => {
        setTermsChecked(e.target.checked);
    };

    //    
    const handlePrivacyChange = (e) => {
        setPrivacyChecked(e.target.checked);
    };

    //    
    const handleMarketingChange = (e) => {
        setMarketingChecked(e.target.checked);
    };

    const onSubmit = (e) => {
        e.preventDefault();

        if (!isValidEmail) {
            alert("  .");
            emailInputRef.current.focus();
            return;
        }
        if (!isValidPassword) {
            alert(" .");
            passwordInputRef.current.focus();
            return;
        }
        if (!isValidPasswordCheck) {
            alert("  .");
            passwordCheckInputRef.current.focus();
            return;
        }
        if (!isValidName) {
            alert(" .");
            nameInputRef.current.focus();
            return;
        }
        if (!isValidPhone) {
            alert("  .");
            phoneInputRef.current.focus();
            return;
        }
        // alert("success");
        // return;

        const loginData = {
            email,
            password,
        };

        dispatch(registerUser(loginData)).then((response) => {
            if (response.payload.loginSuccess) {
                console.log(response);
                //navigate('/login');
            } else {
                alert(response.payload.error);
            }
        });
    };

    return (
        <div>
            <h2 className={RegisterStyle.tit}></h2>
            <form className={RegisterStyle.register_form} name="register_form" onSubmit={onSubmit}>
                <h3></h3>
                <div className={RegisterStyle.essential_area}>
                    <div>
                        <div className={RegisterStyle.label_area}>
                            <label htmlFor="email"></label>
                            <p className={`${RegisterStyle.error} ${RegisterStyle.error_email} ${isValidEmail ? "" : `${RegisterStyle.on}`} `}>   .</p>
                        </div>
                        <input id="email" type="text" placeholder=" ." autoComplete="off" name="email" required onChange={handleEmailInputChange} className={isValidEmail ? "" : `${RegisterStyle.on}`} ref={emailInputRef} value={email} />
                    </div>
                    <div>
                        <div className={RegisterStyle.label_area}>
                            <label htmlFor="password"></label>
                            <p className={`${RegisterStyle.error} ${RegisterStyle.error_password}  ${isValidPassword ? "" : `${RegisterStyle.on}`}`}> , ,   2  8-15</p>
                        </div>
                        <input id="password" type="password" placeholder=" , ,   2  8-15" autoComplete="off" name="password" required onChange={handlePasswordInputChange} className={isValidPassword ? "" : `${RegisterStyle.on}`} ref={passwordInputRef} value={password} />
                    </div>
                    <div>
                        <div className={RegisterStyle.label_area}>
                            <label htmlFor="passwordConfirm"> </label>
                            <p className={`${RegisterStyle.error} ${RegisterStyle.error_confirm} ${isValidPasswordCheck ? "" : `${RegisterStyle.on}`}`}>  .</p>
                        </div>
                        <input id="passwordConfirm" type="password" placeholder="   ." autoComplete="off" name="passwordConfirm" required onChange={handlePasswordCheckInputChange} className={isValidPasswordCheck ? "" : `${RegisterStyle.on}`} ref={passwordCheckInputRef} />
                    </div>
                    <div>
                        <label htmlFor="name"></label>
                        <input id="name" type="text" placeholder=" ." autoComplete="off" name="name" onChange={handleNameInputChange} className={RegisterStyle.essential} required ref={nameInputRef} />
                    </div>
                    <div className={RegisterStyle.border_box}>
                        <label htmlFor="phone"> </label>
                        <input id="phone" type="text" placeholder="  ." autoComplete="off" name="phone" onChange={handlePhoneInputChange} className={RegisterStyle.essential} maxLength="13" required ref={phoneInputRef} />
                    </div>

                    <h3> </h3>
                    <div>
                        <label htmlFor="corpName"></label>
                        <input id="corpName" type="text" placeholder="  ." autoComplete="off" name="corpName" className={RegisterStyle.essential} />
                    </div>
                    <div>
                        <label htmlFor="homepage"> </label>
                        <input id="homepage" type="text" placeholder="   ." autoComplete="off" name="homepage" className={RegisterStyle.essential} />
                    </div>
                    <div>
                        <label htmlFor="address"> </label>
                        <input id="address" type="text" placeholder="   ." autoComplete="off" name="address" className={RegisterStyle.essential} />
                    </div>
                </div>
                <div className={RegisterStyle.vacation}>
                    <span>  </span>
                    <div className={RegisterStyle.radio_wrap}>
                        <div>
                            <label>
                                <input type="radio" name="vacation_type" defaultValue="FINANCE_YEAR" defaultChecked />
                                <span className={RegisterStyle.label}> </span>
                            </label>
                        </div>
                        <div>
                            <label>
                                <input type="radio" name="vacation_type" defaultValue="ENTRYDATE" />
                                <span className={RegisterStyle.label}> </span>
                            </label>
                        </div>
                        <div>
                            <label>
                                <input type="radio" name="vacation_type" defaultValue="FINANCE_YEAR_BY_ENTRYDATE" />
                                <span className={RegisterStyle.label}> (  )</span>
                            </label>
                        </div>
                    </div>
                </div>
                <div>
                    <label htmlFor="registNumber">()</label>
                    <input id="registNumber" type="text" placeholder=" ." autoComplete="off" name="registNumber" maxLength="10" />
                </div>
                <div className={RegisterStyle.border_box}>
                    <label htmlFor="workSectorCode">()</label>
                    <input id="workSectorCode" type="text" placeholder=" ." autoComplete="off" name="workSectorCode" maxLength="6" />
                </div>

                <h3></h3>
                <div className={`${RegisterStyle.checkbox_wrap} ${RegisterStyle.border}`}>
                    <label>
                        <input id="selectAll" type="checkbox" defaultValue="agree_all" name="checkAll" onChange={handleAgreeCheckAllChange} checked={agreeCheckAll} />
                        <span className={RegisterStyle.label}></span>
                    </label>
                    <a href="#"></a>
                </div>
                <div className={RegisterStyle.checkbox_wrap}>
                    <label>
                        <input type="checkbox" className={RegisterStyle.checkbox} name="check01" required onChange={handleTermsChange} checked={termsChecked} />
                        <span className={RegisterStyle.label}>  ()</span>
                    </label>
                    <a href="#"></a>
                </div>
                <div className={RegisterStyle.checkbox_wrap}>
                    <label>
                        <input type="checkbox" className={RegisterStyle.checkbox} name="check02" required onChange={handlePrivacyChange} checked={privacyChecked} />
                        <span className={RegisterStyle.label}>     ()</span>
                    </label>
                    <a href="#"></a>
                </div>
                <div className={`${RegisterStyle.checkbox_wrap} ${RegisterStyle.border_box}`}>
                    <label>
                        <input type="checkbox" className={RegisterStyle.checkbox} name="check03" onChange={handleMarketingChange} checked={marketingChecked} />
                        <span className={RegisterStyle.label}>     ()</span>
                    </label>
                    <a href="#"></a>
                </div>
                <button type="submit" className={RegisterStyle.submit_btn}>
                    
                </button>
            </form>
            {/* 

            <div style={{ display: "flex", justifyContent: "center", alignItems: "center", width: "100%", height: "100vh" }}>
                <form style={{ display: "flex", flexDirection: "column" }} onSubmit={onSubmit}>
                    <input type="text" placeholder="ID" value={email} onChange={(e) => setEmail(e.target.value)} />
                    <input type="password" placeholder="PW" value={password} onChange={(e) => setPassword(e.target.value)} />
                    <button type="submit">Register</button>
                </form>
            </div>	
			*/}
        </div>
    );
}

export default Register;
