import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useCookies } from 'react-cookie';
import { auth } from '../_actions/user_action';
import Empty from '../pages/Empty';

function Auth(ChildrenComponent, option, adminRoute = false, menu = 0, subMenu = 0) {
    function AuthenticationCheck() {
        const navigate = useNavigate();
        const dispatch = useDispatch();
        const [cookies] = useCookies(['x_auth']);

        const [view, setView] = useState(false);

        useEffect(() => {
            dispatch(auth(cookies)).then((response) => {
                if (response.payload.isAuth) {
                    //console.log(response.payload.isAdmin);
                    // 
                    if (adminRoute && !response.payload.isAdmin) {
                        //       
                        alert(' .');
                        navigate('/');
                    } else {
                        //     
                        if (option === false) {
                            navigate('/');
                        }

                        setView(true);
                    }
                } else {
                    // 
                    if (option === true) {
                        alert(' !');
                        navigate('/login');
                    }
                    setView(true);
                }
            });

            if (adminRoute === true) {
                const data = { menu, subMenu };

                //dispatch(adminMenu(data));
            }
        }, [cookies, dispatch]);

        // null ->  
        // true ->   
        // false ->   
        if (view === false) {
            return (
                <div>
                    <Empty></Empty>
                </div>
            );
        } else {
            return (
                <div>
                    <ChildrenComponent></ChildrenComponent>
                </div>
            );
        }
    }

    return (
        <div>
            <AuthenticationCheck></AuthenticationCheck>
        </div>
    );
}

export default Auth;
